import { createRouter, createWebHashHistory } from 'vue-router'
import index from '../views'
import home from '../views/pages/home.vue'
import login from '../views/pages/login.vue'
import newsList from '@/views/pages/news/list'
import messagesList from '@/views/pages/messages/list'
import duzheList from '@/views/pages/duzhe/list'
import duzheDetail from '@/views/pages/duzhe/formModel'
import duzheAdd from '@/views/pages/duzhe/formAdd'
import duzheRegister from '@/views/pages/duzhe/register'
import duzheCenter from '@/views/pages/duzhe/center'
import zuojiaList from '@/views/pages/zuojia/list'
import zuojiaDetail from '@/views/pages/zuojia/formModel'
import zuojiaAdd from '@/views/pages/zuojia/formAdd'
import xiaoshuofenleiList from '@/views/pages/xiaoshuofenlei/list'
import xiaoshuofenleiDetail from '@/views/pages/xiaoshuofenlei/formModel'
import xiaoshuofenleiAdd from '@/views/pages/xiaoshuofenlei/formAdd'
import xiaoshuoxinxiList from '@/views/pages/xiaoshuoxinxi/list'
import xiaoshuoxinxiDetail from '@/views/pages/xiaoshuoxinxi/formModel'
import xiaoshuoxinxiAdd from '@/views/pages/xiaoshuoxinxi/formAdd'
import xiaoshuoxinxiChapter from '@/views/pages/xiaoshuoxinxi/chapter'
import storeupList from '@/views/pages/storeup/list'
import chapterxiaoshuoxinxiList from '@/views/pages/chapterxiaoshuoxinxi/list'
import chapterxiaoshuoxinxiDetail from '@/views/pages/chapterxiaoshuoxinxi/formModel'
import chapterxiaoshuoxinxiAdd from '@/views/pages/chapterxiaoshuoxinxi/formAdd'

const routes = [{
		path: '/',
		redirect: '/index/home'
	},
	{
		path: '/index',
		component: index,
		children: [{
			path: 'home',
			component: home
		}
		, {
			path: 'newsList',
			component: newsList
		}
		, {
			path: 'messagesList',
			component: messagesList
		}
		, {
			path: 'duzheList',
			component: duzheList
		}, {
			path: 'duzheDetail',
			component: duzheDetail
		}, {
			path: 'duzheAdd',
			component: duzheAdd
		}
		, {
			path: 'duzheCenter',
			component: duzheCenter
		}
		, {
			path: 'zuojiaList',
			component: zuojiaList
		}, {
			path: 'zuojiaDetail',
			component: zuojiaDetail
		}, {
			path: 'zuojiaAdd',
			component: zuojiaAdd
		}
		, {
			path: 'xiaoshuofenleiList',
			component: xiaoshuofenleiList
		}, {
			path: 'xiaoshuofenleiDetail',
			component: xiaoshuofenleiDetail
		}, {
			path: 'xiaoshuofenleiAdd',
			component: xiaoshuofenleiAdd
		}
		, {
			path: 'xiaoshuoxinxiList',
			component: xiaoshuoxinxiList
		}, {
			path: 'xiaoshuoxinxiDetail',
			component: xiaoshuoxinxiDetail
		}, {
			path: 'xiaoshuoxinxiAdd',
			component: xiaoshuoxinxiAdd
		}
		, {
			path: 'xiaoshuoxinxiChapter',
			component: xiaoshuoxinxiChapter
		}
		, {
			path: 'storeupList',
			component: storeupList
		}
		, {
			path: 'chapterxiaoshuoxinxiList',
			component: chapterxiaoshuoxinxiList
		}, {
			path: 'chapterxiaoshuoxinxiDetail',
			component: chapterxiaoshuoxinxiDetail
		}, {
			path: 'chapterxiaoshuoxinxiAdd',
			component: chapterxiaoshuoxinxiAdd
		}
		]
	},
	{
		path: '/login',
		component: login
	}
	,{
		path: '/duzheRegister',
		component: duzheRegister
	}
]

const router = createRouter({
  history: createWebHashHistory(process.env.BASE_URL),
  routes
})

export default router
